import Evaluating from '@/components/common/Evaluating/components/myIssue.vue';
export default {
	props: ['inputName','feedNum'],
	data() {
		return {
			zi:'机扫打飞机扫打飞机扫打飞机公司的附近公飞机扫打飞机扫打飞机扫打飞机公司的附近公飞机扫打飞机扫打飞机扫打飞机公司的附近公飞',
			up: this.Static+'my/up-arrow.png.png',
			deletes: this.Static+'my/delete.png',
			problem:[],
			xin:'',
			arr:[],
		};
	},
	components: {
		Evaluating,
	},
	computed:{
		i18n () {
		  return this.$t('Mypersonal')  
		},
	},
	created() {
		this.assemblys();
	},
	filters: {
		formatDate: function(value) {
			let date = new Date(value);
			let y = date.getFullYear();
			let MM = date.getMonth() + 1;
			MM = MM < 10 ? ('0' + MM) : MM;
			let d = date.getDate();
			d = d < 10 ? ('0' + d) : d;
			let h = date.getHours();
			h = h < 10 ? ('0' + h) : h;
			let m = date.getMinutes();
			m = m < 10 ? ('0' + m) : m;
			let s = date.getSeconds();
			s = s < 10 ? ('0' + s) : s;
			return y + '.' + MM + '.' + d ;
		}
	},
	methods: {
		img001(e,key){
			console.log(e)
			uni.previewImage({
				urls: e,
				current: e[key],
				indicator: "number",
				loop: "true",
				longPressActions: {
					success: function(data) {
						console.log('选中了第' + (data.tapIndex + 1) + '个按钮,第' + (data.index + 1) + '张图片');
					},
					fail: function(err) {
						console.log(err.errMsg);
					}
				}
			});
		},
		assemblys(){
			if (uni.getStorageSync('landRegist')) {
				let landRegistLG = JSON.parse(uni.getStorageSync('landRegist')); // 读取缓存的用户信息
				console.log(landRegistLG.user.id);
				// let params = {}; // 请求总数居时 参数为空
				uni.showLoading({ // 展示loading
					title: '加载中'
				});
